@media screen and (min-width: 999px) {
  .post-right-bottom-p{
    font-size: 1.3em;
  }

  .post-right-top-h{
    font-size: 1em;
  }
  
  .buttons{
    font-size: 1em;
    padding: 1em;
    margin-left: 0;
  }

  .previous-button{
    margin-left: 0;
  }

  .next-button{
    margin-left: 0em;
  }

  .next-previous-section{
    inline-size: min-content;
  }

  .make-post-text{
    font-size: 2em;
  }

  .post {
    // height: fit-content;
    // height: auto;
    max-height: 187px;
    // max-width: 66em;
    max-width: 75%;
    margin-left: 20%;
    margin-right: 20%;
  }

  .post-img {
    margin-top: 1em;
  }
}

@media screen and (max-width: 435px){
  .previous-button{
    margin-right: 7em;
  }
}

@media screen and (max-width: 377px){
  .previous-button{
    margin-right: 5em;
  }
}